<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    #t_2_5
    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.5. Comunicación efectiva y presentación personal del guía

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p La comunicación es una herramienta esencial para el guía o intérprete turístico, pues de ella depende en gran medida la calidad de la experiencia del visitante. La Organización Mundial del Turismo (OMT, 2015) señala que “una comunicación eficaz durante la interpretación turística contribuye a generar comprensión, respeto y aprecio hacia los valores culturales y naturales del destino”. En este sentido, el guía no solo transmite información, sino que se convierte en un mediador entre el visitante, la comunidad anfitriona y el entorno, favoreciendo la educación, la empatía y la sostenibilidad. A continuación se indican cuáles son esos aspectos que el guía debe cuidar, con la finalidad de proyectar seguridad y confianza al público:

    .tarjeta(titulo="" data-aos="fade-right")
      .imgCont.m-auto
        .row.justify-content-center
          ImagenInfografica.color-acento-contenido
            template(v-slot:imagen)
              figure
                img(src='@/assets/curso/temas/tema2/tema6/rec19-fondo.svg', alt='Texto que describa la imagen')
          
            .tarjeta.color-acento-botones.p-3(x="44%" y="8%" numero="1")
              h5 Comunicación efectiva
              p Se basa en tres pilares fundamentales: claridad, empatía y adaptabilidad. El guía debe expresarse con lenguaje comprensible, evitar tecnicismos innecesarios y adaptar su discurso al tipo de público (niños, adultos mayores, extranjeros, personas con necesidades especiales, entre otros).

            .tarjeta.color-acento-botones.p-3(x="70%" y="35%" numero="2")
              h5 Escuchar activamente
              p También es indispensable para comunicarse, responder dudas, detectar emociones y ajustar el ritmo o tono según la situación. Por ejemplo, durante un recorrido por un parque natural, puede reforzar su mensaje ambiental al percibir un interés genuino en los visitantes, promoviendo conductas responsables y sostenibles.

            .tarjeta.color-acento-botones.p-3(x="30%" y="52%" numero="3")
              h5 Componente no verbal
              p El componente no verbal también cumple un papel crucial. Según Albert Mehrabian (1972), el 93 % de la comunicación interpersonal está influenciada por el tono de voz, los gestos y la postura corporal. En un recorrido turístico, la sonrisa, el contacto visual y una actitud positiva fortalecen la confianza del grupo y refuerzan la autoridad del guía sin imponerla.

            .tarjeta.color-acento-botones.p-3(x="61%" y="75%" numero="4")
              h5 La presentación personal
              .p Un guía profesional debe transmitir orden, seguridad y confianza. 
              p Para ello debe proyectar limpieza, pulcritud, organización y estar en coherencia con el tipo de actividad que realiza. La vestimenta y los accesorios deben combinar comodidad, funcionalidad y respeto cultural. Por ejemplo, un guía en recorridos urbanos puede usar uniforme institucional con credencial visible, mientras que en actividades de ecoturismo o senderismo debe priorizar calzado resistente, sombrero, protección solar y ropa adecuada al clima.

            .tarjeta.color-acento-botones.p-3(x="38%" y="90%" numero="5")
              p Asimismo, la OMT (2019) recomienda que los guías turísticos adopten una comunicación intercultural, entendida como la capacidad de interactuar con respeto hacia visitantes de diversas nacionalidades, religiones o costumbres. En contextos étnicos o comunitarios, el guía se convierte en un portavoz cultural, por lo que debe emplear un lenguaje inclusivo, reconocer los saberes locales y promover el diálogo entre culturas.

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Finalmente, la comunicación efectiva y la presentación personal no son elementos aislados, sino componentes integrales del servicio turístico. Un guía que comunica con empatía, domina su lenguaje corporal y mantiene una imagen profesional logra que los visitantes se sientan atendidos, comprendidos y motivados a cuidar los lugares que visitan.
      p De este modo, la comunicación se convierte en una herramienta pedagógica y de sostenibilidad.
    
